'use client'

import { useState } from 'react'
import { getImageUrl } from '@/lib/api'
import { useProfile } from '@/contexts/ProfileContext'
import { useSocialLinks } from '@/contexts/SocialLinksContext'
import { useSettings } from '@/contexts/SettingsContext'
import { getFileUrl } from '@/lib/utils'
import { X } from 'lucide-react'

export default function BlogProfile() {
  const { profile, isLoading: profileLoading } = useProfile()
  const { profileSocialLinks: socialLinks } = useSocialLinks()
  const [showQRModal, setShowQRModal] = useState(false)
  const [selectedQRCode, setSelectedQRCode] = useState<{ image: string; platform: string } | null>(null)
  const [showAccountModal, setShowAccountModal] = useState(false)
  const [selectedAccount, setSelectedAccount] = useState<{ account: string; platform: string } | null>(null)
  const [copySuccess, setCopySuccess] = useState(false)
  const [showLinkModal, setShowLinkModal] = useState(false)
  const [selectedLink, setSelectedLink] = useState<{ url: string; platform: string } | null>(null)
  const { settings } = useSettings()

  const avatarShape = settings.avatarShape || 'circle'
  const loading = profileLoading

  const getAvatarClass = () => {
    return avatarShape === 'rounded' ? 'rounded-xl' : 'rounded-full'
  }

  const handleSocialLinkClick = (e: React.MouseEvent, link: any) => {
    // 如果有二维码，显示二维码弹窗，不跳转
    if (link.qrcode && link.qrcode.trim() !== '') {
      e.preventDefault()
      setSelectedQRCode({
        image: link.qrcode,
        platform: link.platform
      })
      setShowQRModal(true)
    } else if (!link.link || link.link.trim() === '' || link.link === '#') {
      // 如果没有链接，显示账号弹窗
      e.preventDefault()
      setSelectedAccount({
        account: link.account,
        platform: link.platform
      })
      setShowAccountModal(true)
    } else {
      // 有链接时，显示跳转确认弹窗
      e.preventDefault()
      setSelectedLink({
        url: link.link,
        platform: link.platform
      })
      setShowLinkModal(true)
    }
  }

  const closeQRModal = () => {
    setShowQRModal(false)
    setTimeout(() => setSelectedQRCode(null), 300)
  }

  const closeAccountModal = () => {
    setShowAccountModal(false)
    setCopySuccess(false)
    setTimeout(() => setSelectedAccount(null), 300)
  }

  const closeLinkModal = () => {
    setShowLinkModal(false)
    setTimeout(() => setSelectedLink(null), 300)
  }

  const handleConfirmLink = () => {
    if (selectedLink) {
      window.open(selectedLink.url, '_blank', 'noopener,noreferrer')
    }
    closeLinkModal()
  }

  const handleCopyAccount = async () => {
    if (!selectedAccount) return
    try {
      await navigator.clipboard.writeText(selectedAccount.account)
      setCopySuccess(true)
      setTimeout(() => setCopySuccess(false), 2000)
    } catch (error) {
      console.error('Failed to copy:', error)
    }
  }

  if (loading) {
    return (
      <div className="bg-bg-secondary rounded-lg shadow-sm p-6 border border-border-primary h-full">
        <div className="flex flex-col items-center space-y-4">
          <div className="w-20 h-20 bg-bg-tertiary rounded-full animate-pulse"></div>
          <div className="w-full space-y-2">
            <div className="h-6 w-32 bg-bg-tertiary animate-pulse mx-auto"></div>
            <div className="h-4 w-48 bg-bg-tertiary animate-pulse mx-auto"></div>
          </div>
        </div>
      </div>
    )
  }

  if (!profile) {
    return null
  }

  return (
    <div className="bg-bg-secondary rounded-lg shadow-sm hover:shadow-lg transition-all duration-300 p-6 border border-border-primary h-full flex flex-col justify-center">
      {/* 头像 - 居中 */}
      <div className="flex justify-center mb-4">
        {profile.avatar ? (
          <img
            src={getImageUrl(profile.avatar)}
            alt={profile.name || '头像'}
            className={`w-20 h-20 ${getAvatarClass()} object-cover shadow-lg`}
            style={{ boxShadow: '0 3px 10px 0 rgba(0, 0, 0, 0.12)' }}
          />
        ) : (
          <div
            className={`w-20 h-20 ${getAvatarClass()} bg-gradient-to-br from-primary-400 to-primary-600 flex items-center justify-center text-white text-2xl font-bold shadow-lg`}
            style={{ boxShadow: '0 3px 10px 0 rgba(0, 0, 0, 0.12)' }}
          >
            {profile.name?.[0] || 'X'}
          </div>
        )}
      </div>

      {/* 个人信息 - 居中 */}
      <div className="text-center mb-4">
        <h3 className="text-xl font-semibold text-text-primary">
          {settings.siteTitle || profile.name || ''}
        </h3>

        {profile.title && (
          <p className="text-sm text-text-secondary mt-2">
            {profile.title}
          </p>
        )}

        {profile.bio && (
          <p className="text-sm text-text-tertiary mt-3 line-clamp-3">
            {profile.bio}
          </p>
        )}

      </div>

      {/* 联系信息 - 居中 */}
      <div className="flex flex-wrap justify-center gap-3 mb-4">
        {[
          profile.location && (
            <div key="location" className="flex items-center text-xs text-text-tertiary">
              <svg
                className="w-3 h-3 mr-1"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z"
                />
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M15 11a3 3 0 11-6 0 3 3 0 016 0z"
                />
              </svg>
              {profile.location}
            </div>
          ),
          profile.email && (
            <div key="email" className="flex items-center text-xs text-text-tertiary">
              <svg
                className="w-3 h-3 mr-1"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z"
                />
              </svg>
              {profile.email}
            </div>
          ),
          profile.website && (
            <a
              key="website"
              href={profile.website}
              target="_blank"
              rel="noopener noreferrer"
              className="flex items-center text-xs text-primary-600 hover:text-primary-700 transition-colors"
            >
              <svg
                className="w-3 h-3 mr-1"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M21 12a9 9 0 01-9 9m9-9a9 9 0 00-9-9m9 9H3m9 9a9 9 0 01-9-9m9 9c1.657 0 3-4.03 3-9s-1.343-9-3-9m0 18c-1.657 0-3-4.03-3-9s1.343-9 3-9m-9 9a9 9 0 019-9"
                />
              </svg>
              {profile.website_title || '个人网站'}
            </a>
          )
        ].filter(Boolean)}
      </div>

      {/* 社交链接 - 最多显示4个，均匀分布，自适应大小 */}
      {socialLinks.length > 0 && (
        <div className={`flex items-center pt-5 border-t border-border-primary/20 px-8 ${
          socialLinks.length === 1 ? 'justify-center' : 'justify-between'
        }`}>
          {socialLinks.map((link, index) => (
            <a
              key={`social-link-${link.id}-${index}`}
              href={link.link || '#'}
              target={link.link ? '_blank' : '_self'}
              rel="noopener noreferrer"
              onClick={(e) => handleSocialLinkClick(e, link)}
              className="group relative flex items-center justify-center transition-all hover:scale-110 hover:opacity-80 cursor-pointer"
              title={`${link.platform}: ${link.account}`}
            >
              {link.icon && link.icon.trim() !== '' ? (
                <div className={`flex items-center justify-center ${
                  socialLinks.length === 1 ? 'w-10 h-10' :
                  socialLinks.length === 2 ? 'w-9 h-9' :
                  socialLinks.length === 3 ? 'w-8 h-8' :
                  'w-7 h-7'
                }`}>
                  <img
                    src={getFileUrl(link.icon)}
                    alt={link.platform}
                    className="w-full h-full object-contain transition-all group-hover:brightness-110 icon-themed"
                    onError={(e) => {
                      (e.target as HTMLImageElement).style.display = 'none'
                    }}
                  />
                </div>
              ) : (
                <div className={`rounded-full bg-primary-500/20 flex items-center justify-center text-primary-500 font-bold group-hover:bg-primary-500/30 transition-colors ${
                  socialLinks.length === 1 ? 'w-10 h-10 text-base' :
                  socialLinks.length === 2 ? 'w-9 h-9 text-sm' :
                  socialLinks.length === 3 ? 'w-8 h-8 text-sm' :
                  'w-7 h-7 text-xs'
                }`}>
                  {link.platform[0].toUpperCase()}
                </div>
              )}
            </a>
          ))}
        </div>
      )}

      {/* QR Code Modal */}
      {showQRModal && selectedQRCode && (
        <div
          className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black/60 backdrop-blur-sm"
          onClick={closeQRModal}
        >
          <div
            className="relative bg-white dark:bg-gray-800 rounded-2xl shadow-2xl p-6 max-w-sm w-full"
            onClick={(e) => e.stopPropagation()}
          >
            <button
              onClick={closeQRModal}
              className="absolute top-4 right-4 p-2 rounded-full hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors"
              aria-label="关闭"
            >
              <X className="w-5 h-5 text-gray-500 dark:text-gray-400" />
            </button>

            <div className="text-center">
              <h3 className="text-xl font-bold text-gray-900 dark:text-white mb-4 capitalize">
                {selectedQRCode.platform}
              </h3>
              <div className="bg-white p-4 rounded-xl inline-block">
                <img
                  src={getFileUrl(selectedQRCode.image)}
                  alt={`${selectedQRCode.platform} QR Code`}
                  className="w-64 h-64 object-contain"
                />
              </div>
              <p className="text-sm text-gray-500 dark:text-gray-400 mt-4">
                扫描二维码添加联系方式
              </p>
            </div>
          </div>
        </div>
      )}

      {/* Account Modal */}
      {showAccountModal && selectedAccount && (
        <div
          className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black/60 backdrop-blur-sm"
          onClick={closeAccountModal}
        >
          <div
            className="relative bg-white dark:bg-gray-800 rounded-2xl shadow-2xl p-6 max-w-sm w-full"
            onClick={(e) => e.stopPropagation()}
          >
            <button
              onClick={closeAccountModal}
              className="absolute top-4 right-4 p-2 rounded-full hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors"
              aria-label="关闭"
            >
              <X className="w-5 h-5 text-gray-500 dark:text-gray-400" />
            </button>

            <div className="text-center">
              <h3 className="text-xl font-bold text-gray-900 dark:text-white mb-4 capitalize">
                {selectedAccount.platform}
              </h3>
              <div className="bg-gray-50 dark:bg-gray-700 p-4 rounded-xl mb-4">
                <p className="text-lg font-mono text-gray-900 dark:text-white break-all">
                  {selectedAccount.account}
                </p>
              </div>
              <button
                onClick={handleCopyAccount}
                className={`w-full py-3 px-4 rounded-lg font-medium transition-colors ${
                  copySuccess
                    ? 'bg-green-500 text-white'
                    : 'bg-primary-500 hover:bg-primary-600 text-white'
                }`}
              >
                {copySuccess ? '✓ 已复制' : '复制账号'}
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Link Confirm Modal */}
      {showLinkModal && selectedLink && (
        <div
          className="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black/60 backdrop-blur-sm"
          onClick={closeLinkModal}
        >
          <div
            className="relative bg-white dark:bg-gray-800 rounded-2xl shadow-2xl p-6 max-w-sm w-full"
            onClick={(e) => e.stopPropagation()}
          >
            <button
              onClick={closeLinkModal}
              className="absolute top-4 right-4 p-2 rounded-full hover:bg-gray-100 dark:hover:bg-gray-700 transition-colors"
              aria-label="关闭"
            >
              <X className="w-5 h-5 text-gray-500 dark:text-gray-400" />
            </button>

            <div className="text-center">
              <h3 className="text-xl font-bold text-gray-900 dark:text-white mb-4 capitalize">
                {selectedLink.platform}
              </h3>
              <p className="text-gray-600 dark:text-gray-300 mb-4">
                是否跳转到以下链接？
              </p>
              <div className="bg-gray-50 dark:bg-gray-700 p-3 rounded-xl mb-4">
                <p className="text-sm font-mono text-gray-900 dark:text-white break-all">
                  {selectedLink.url}
                </p>
              </div>
              <div className="flex gap-3">
                <button
                  onClick={closeLinkModal}
                  className="flex-1 py-3 px-4 rounded-lg font-medium transition-colors bg-gray-200 hover:bg-gray-300 dark:bg-gray-600 dark:hover:bg-gray-500 text-gray-800 dark:text-white"
                >
                  取消
                </button>
                <button
                  onClick={handleConfirmLink}
                  className="flex-1 py-3 px-4 rounded-lg font-medium transition-colors bg-primary-500 hover:bg-primary-600 text-white"
                >
                  确认跳转
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}