'use client'

import { useState, useEffect, useRef, useCallback } from 'react'

interface AnnouncementItem {
  text: string
  url?: string
}

interface BlogAnnouncementProps {
  announcements: AnnouncementItem[]
}

// 跑马灯动画时长（秒）
const MARQUEE_DURATION = 15
// 滚动前等待时间（毫秒）
const SCROLL_DELAY = 1500

export default function BlogAnnouncement({ announcements }: BlogAnnouncementProps) {
  const [currentIndex, setCurrentIndex] = useState(0)
  const [isAnimating, setIsAnimating] = useState(false)
  const [needsMarquee, setNeedsMarquee] = useState(false)
  const [isScrolling, setIsScrolling] = useState(false) // 是否正在滚动
  const [marqueeKey, setMarqueeKey] = useState(0) // 用于重置跑马灯动画
  const textRef = useRef<HTMLSpanElement>(null)
  const containerRef = useRef<HTMLDivElement>(null)
  const switchTimerRef = useRef<NodeJS.Timeout | null>(null)
  const scrollDelayTimerRef = useRef<NodeJS.Timeout | null>(null)

  // 过滤空公告
  const validAnnouncements = announcements.filter(a => a && a.text && a.text.trim())

  // 只有一条公告时不需要滚动
  const shouldScroll = validAnnouncements.length > 1

  // 切换到下一条公告
  const switchToNext = useCallback(() => {
    if (validAnnouncements.length <= 1) return

    setIsAnimating(true)
    setIsScrolling(false) // 停止滚动

    setTimeout(() => {
      setCurrentIndex(prev => (prev + 1) % validAnnouncements.length)
      setIsAnimating(false)
      setMarqueeKey(prev => prev + 1) // 重置跑马灯动画
    }, 300)
  }, [validAnnouncements.length])

  // 检测文字是否需要跑马灯
  useEffect(() => {
    const checkOverflow = () => {
      if (textRef.current && containerRef.current) {
        const textWidth = textRef.current.scrollWidth
        const containerWidth = containerRef.current.clientWidth
        // 只有多条公告时才检测是否需要滚动
        setNeedsMarquee(shouldScroll && textWidth > containerWidth - 20)
      }
    }

    // 延迟检测，确保DOM已渲染
    const timer = setTimeout(checkOverflow, 100)
    window.addEventListener('resize', checkOverflow)
    return () => {
      clearTimeout(timer)
      window.removeEventListener('resize', checkOverflow)
    }
  }, [currentIndex, validAnnouncements, shouldScroll])

  // 每条公告的滚动延迟启动（1.5秒后开始滚动）
  useEffect(() => {
    // 只有一条公告时不执行滚动
    if (!shouldScroll) {
      setIsScrolling(false)
      return
    }

    // 清除之前的延迟定时器
    if (scrollDelayTimerRef.current) {
      clearTimeout(scrollDelayTimerRef.current)
    }

    // 每条公告先暂停1.5秒后再开始滚动
    scrollDelayTimerRef.current = setTimeout(() => {
      setIsScrolling(true)
    }, SCROLL_DELAY)

    return () => {
      if (scrollDelayTimerRef.current) {
        clearTimeout(scrollDelayTimerRef.current)
      }
    }
  }, [currentIndex, marqueeKey, shouldScroll])

  // 多条公告自动切换（滚动完成后切换）
  useEffect(() => {
    if (validAnnouncements.length <= 1) return
    if (!isScrolling) return

    // 清除之前的定时器
    if (switchTimerRef.current) {
      clearTimeout(switchTimerRef.current)
    }

    // 计算切换延迟时间：滚动动画完成后切换
    let switchDelay: number
    if (needsMarquee) {
      // 需要跑马灯：等待动画完成后切换
      switchDelay = MARQUEE_DURATION * 1000
    } else {
      // 不需要跑马灯：固定3.5秒后切换（加上1.5秒延迟共5秒）
      switchDelay = 3500
    }

    switchTimerRef.current = setTimeout(() => {
      switchToNext()
    }, switchDelay)

    return () => {
      if (switchTimerRef.current) {
        clearTimeout(switchTimerRef.current)
      }
    }
  }, [validAnnouncements.length, needsMarquee, isScrolling, switchToNext])

  if (validAnnouncements.length === 0) return null

  const currentAnnouncement = validAnnouncements[currentIndex]

  const handleClick = () => {
    if (currentAnnouncement.url) {
      window.open(currentAnnouncement.url, '_blank', 'noopener,noreferrer')
    }
  }

  // 手动切换公告
  const handleIndicatorClick = (e: React.MouseEvent, index: number) => {
    e.stopPropagation()
    if (index === currentIndex) return

    // 清除定时器
    if (switchTimerRef.current) {
      clearTimeout(switchTimerRef.current)
    }
    if (scrollDelayTimerRef.current) {
      clearTimeout(scrollDelayTimerRef.current)
    }

    setIsAnimating(true)
    setIsScrolling(false)

    setTimeout(() => {
      setCurrentIndex(index)
      setIsAnimating(false)
      setMarqueeKey(prev => prev + 1)
    }, 300)
  }

  return (
    <div
      className={`w-full bg-bg-secondary/80 border border-border-primary rounded-xl shadow-sm backdrop-blur-sm overflow-hidden transition-all duration-300 ${
        currentAnnouncement.url ? 'cursor-pointer hover:bg-bg-tertiary/80 hover:shadow-md' : ''
      }`}
      onClick={handleClick}
    >
      <div className="flex items-center px-4 py-3 gap-3">
        {/* 公告标签 */}
        <span className="flex-shrink-0 text-xs font-bold text-white bg-gradient-to-r from-blue-500 to-indigo-500 px-2.5 py-1 rounded-md shadow-sm">
          公告
        </span>

        {/* 公告内容 */}
        <div
          ref={containerRef}
          className="flex-1 overflow-hidden relative min-w-0"
        >
          <div
            className={`transition-all duration-300 ease-in-out ${
              isAnimating ? 'opacity-0 -translate-y-3' : 'opacity-100 translate-y-0'
            }`}
          >
            <div className={`overflow-hidden ${needsMarquee && isScrolling ? 'marquee-container' : ''}`}>
              <span
                ref={textRef}
                className={`text-sm font-medium text-text-primary whitespace-nowrap inline-block ${
                  needsMarquee && isScrolling ? 'marquee-text' : ''
                }`}
              >
                {currentAnnouncement.text}
              </span>
              {needsMarquee && isScrolling && (
                <span className="text-sm font-medium text-text-primary whitespace-nowrap inline-block marquee-text-copy">
                  {currentAnnouncement.text}
                </span>
              )}
            </div>
          </div>
        </div>

        {/* 公告数量指示器 */}
        {validAnnouncements.length > 1 && (
          <div className="flex-shrink-0 flex items-center gap-1.5">
            {validAnnouncements.map((_, index) => (
              <button
                key={index}
                onClick={(e) => handleIndicatorClick(e, index)}
                className={`h-2 rounded-full transition-all duration-300 ${
                  index === currentIndex
                    ? 'bg-primary w-4'
                    : 'bg-text-tertiary/40 hover:bg-text-tertiary/60 w-2'
                }`}
              />
            ))}
          </div>
        )}

        {/* 链接箭头指示 */}
        {currentAnnouncement.url && (
          <svg className="w-4 h-4 text-text-tertiary flex-shrink-0" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
          </svg>
        )}
      </div>

      {/* 跑马灯动画样式 */}
      <style jsx>{`
        .marquee-container {
          display: flex;
          gap: 80px;
        }
        .marquee-text,
        .marquee-text-copy {
          animation: marquee ${MARQUEE_DURATION}s linear forwards;
        }
        @keyframes marquee {
          0% {
            transform: translateX(0);
          }
          100% {
            transform: translateX(calc(-100% - 80px));
          }
        }
      `}</style>
    </div>
  )
}
