'use client';

import React, { useEffect, useState } from 'react';
import { motion } from 'framer-motion';
import { Clock, Pin } from 'lucide-react';
import { usePathname, useRouter } from 'next/navigation';
import api from '@/lib/api';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import rehypeRaw from 'rehype-raw';
import Masonry from 'react-masonry-css';

import { getApiBaseUrl, safeFetch } from '@/lib/utils';
import { isAdminPage } from '@/lib/adminConfig';
import DiskModal from '@/components/DiskModal';
import NoteShareCard from '@/components/NoteShareCard';
import CopyButton from '@/components/CopyButton';

const API_BASE_URL = getApiBaseUrl();

interface Note {
  id: number;
  title: string;
  content: string;
  summary?: string;
  is_published: boolean;
  is_pinned?: boolean;
  published_at?: string;
  created_at: string;
  media_type?: 'none' | 'image' | 'video' | 'music';
  media_urls?: string[];
  external_link?: string;
  category?: string;
  category_id?: number;
  tags?: string;
  password?: string;
  requiresPassword?: boolean;
  cover_image?: string;
  Tags?: Array<{
    id: number;
    name: string;
    color?: string;
  }>;
  source_type?: 'none' | 'original' | 'reprint';
  source_url?: string;
  source_text?: string;
}

interface NoteCategory {
  id: number;
  name: string;
  type?: string;
  description?: string;
  icon?: string;
  sort_order?: number;
  parent_id?: number;
  created_at?: string;
  updated_at?: string;
}

interface NotesProps {
  searchQuery?: string;
}

const formatDateTime = (dateString: string) => {
  const date = new Date(dateString);
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0');
  const day = String(date.getDate()).padStart(2, '0');
  const hours = String(date.getHours()).padStart(2, '0');
  const minutes = String(date.getMinutes()).padStart(2, '0');

  return `${year}-${month}-${day} ${hours}:${minutes}`;
};

const truncate = (str: string, length: number) => {
  if (str.length <= length) return str;
  return str.substring(0, length) + '...';
};

// 过滤markdown符号，返回纯文本内容
const stripMarkdown = (markdown: string): string => {
  if (!markdown) return '';

  let text = markdown;

  // 首先移除所有图片语法（必须在移除链接之前处理）
  // 1. 标准图片语法：![alt text](url "optional title")
  text = text.replace(/!\[([^\]]*)\]\(([^)]+)\)/g, '');
  // 2. 引用式图片：![alt text][id]
  text = text.replace(/!\[([^\]]*)\]\[([^\]]+)\]/g, '');
  // 3. 简写引用式图片：![id]
  text = text.replace(/!\[([^\]]+)\]/g, '');
  // 4. 清理可能残留的感叹号和方括号组合
  text = text.replace(/!\s*\[/g, '');

  // 移除代码块符号 (```code```)
  text = text.replace(/```[\s\S]*?```/g, '');

  // 移除内联代码符号 (`code`)
  text = text.replace(/`([^`]+)`/g, '$1');
  text = text.replace(/`/g, '');

  // 移除标题符号 (#, ##, ###)
  text = text.replace(/^#{1,6}\s+/gm, '');

  // 移除粗体符号 (**text**, __text__)
  text = text.replace(/\*\*([^\*]+)\*\*/g, '$1');
  text = text.replace(/__([^_]+)__/g, '$1');

  // 移除斜体符号 (*text*, _text_)
  text = text.replace(/\*([^\*]+)\*/g, '$1');
  text = text.replace(/_([^_]+)_/g, '$1');

  // 移除删除线符号 (~~text~~)
  text = text.replace(/~~([^~]+)~~/g, '$1');

  // 移除列表符号 (- item, * item, 1. item)
  text = text.replace(/^[\s]*[-*+]\s+/gm, '');
  text = text.replace(/^[\s]*\d+\.\s+/gm, '');

  // 移除引用符号 (> text)
  text = text.replace(/^>\s+/gm, '');

  // 移除链接符号 ([text](url))，保留链接文本
  text = text.replace(/\[([^\]]+)\]\(([^)]+)\)/g, '$1');

  // 移除表格符号 (| --- |)
  text = text.replace(/\|.*\|/g, '');

  // 移除水平分割线 (---, ***, ___)
  text = text.replace(/^\s*[-*_]{3,}\s*$/gm, '');

  // 清理多余的空格和换行
  text = text
    .replace(/\s+/g, ' ') // 多个空格合并为一个
    .replace(/\n{3,}/g, '\n\n') // 多个换行合并为两个
    .replace(/\n\s*\n/g, '\n\n') // 清理换行间的空格
    .trim();

  return text;
};

export default function Notes({ searchQuery = '' }: NotesProps) {
  const pathname = usePathname();
  const router = useRouter();
  const isAdmin = pathname ? isAdminPage(pathname) : false;
  const [notes, setNotes] = useState<Note[]>([]);
  const [categories, setCategories] = useState<NoteCategory[]>([]);
  const [loading, setLoading] = useState(true);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [selectedNote, setSelectedNote] = useState<Note | null>(null);
  const [showModal, setShowModal] = useState(false);
  const [showPasswordModal, setShowPasswordModal] = useState(false);
  const [password, setPassword] = useState('');
  const [passwordError, setPasswordError] = useState('');
  const [verifying, setVerifying] = useState(false);
  const [urlMetadata, setUrlMetadata] = useState<Record<string, { title: string; description: string; favicon: string; image: string }>>({});
  const [settings, setSettings] = useState({
    showNoteTags: true,
    showNoteCategories: true,
    noteDisplayMode: 'modal', // 笔记展示模式
    noteLayoutColumns: '1', // 笔记列表布局列数
    showNoteCover: 'true', // 是否显示笔记封面
    defaultNoteCover: '' // 默认封面图
  });
  const [filter, setFilter] = useState<{
    type: 'tag' | 'category' | null;
    value: string | null;
  }>({
    type: null,
    value: null
  });

  // 网盘相关状态
  const [showDiskModal, setShowDiskModal] = useState(false);
  const [hasDisks, setHasDisks] = useState(false);
  const [verifiedPassword, setVerifiedPassword] = useState<string>('');

  // 分享卡片状态
  const [showShareCard, setShowShareCard] = useState(false);

  // 判断当前是否在首页
  const isHomePage = pathname === '/';

  // 检测屏幕尺寸
  const [isDesktop, setIsDesktop] = useState(false);

  useEffect(() => {
    const checkScreenSize = () => {
      setIsDesktop(window.innerWidth >= 768);
    };

    checkScreenSize();
    window.addEventListener('resize', checkScreenSize);

    return () => window.removeEventListener('resize', checkScreenSize);
  }, []);

  useEffect(() => {
    fetchNotes();
    fetchCategories();
    loadSettings();
  }, [page, filter]);

  const fetchNotes = async () => {
    try {
      const params: any = {
        is_published: true,
        page,
        limit: 6,
      };

      // 添加筛选参数
      if (filter.type && filter.value) {
        if (filter.type === 'tag') {
          params.tag = filter.value;
        } else if (filter.type === 'category') {
          params.category = filter.value;
        }
      }

      const response = await api.get('/notes', { params });
      const data = response.data?.data;
      const notesArray = Array.isArray(data?.notes) ? data.notes : [];
      setNotes(notesArray);
      setTotalPages(data?.pagination?.totalPages || 1);
    } catch (error) {
      console.error('Failed to fetch notes:', error);
      setNotes([]);
    } finally {
      setLoading(false);
    }
  };

  const fetchCategories = async () => {
    try {
      const response = await api.get('/notes/categories/list');
      const data = response.data?.data;
      setCategories(Array.isArray(data) ? data : []);
    } catch (error) {
      console.error('Failed to fetch categories:', error);
      setCategories([]);
    }
  };

  const handleNoteClick = async (note: Note) => {
    // 根据设置的展示模式决定行为
    if (settings.noteDisplayMode === 'page') {
      // 页面模式：跳转到独立页面
      window.location.href = `/note/${note.id}`;
    } else {
      // 窗口模式：先检查是否需要密码
      try {
        const response = await api.get(`/notes/${note.id}`);
        if (response.data?.success && response.data?.data) {
          const noteData = response.data.data;

          // 检查是否有网盘资源
          await checkNoteDisks(note.id);

          // 如果需要密码，显示密码输入框
          if (noteData.requiresPassword) {
            setSelectedNote(noteData);
            setShowPasswordModal(true);
          } else {
            // 无需密码，直接显示内容
            setSelectedNote(noteData);
            setShowModal(true);
          }
        }
      } catch (error) {
        console.error('Failed to fetch note:', error);
      }
    }
  };

  // 检查笔记是否有网盘资源
  const checkNoteDisks = async (noteId: number, password?: string) => {
    try {
      const passwordParam = password ? `?password=${encodeURIComponent(password)}` : '';
      const response = await api.get(`/notes/${noteId}/disks${passwordParam}`);
      if (response.data?.success && response.data?.data) {
        setHasDisks(response.data.data.length > 0);
      }
    } catch (error) {
      console.error('Failed to check disks:', error);
      setHasDisks(false);
    }
  };

  // 验证密码
  const handlePasswordSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!selectedNote) return;

    setPasswordError('');
    setVerifying(true);

    try {
      const response = await api.post(`/notes/${selectedNote.id}/verify`, { password });
      if (response.data?.success && response.data?.data) {
        setSelectedNote(response.data.data);
        setShowPasswordModal(false);
        setShowModal(true);
        setVerifiedPassword(password);
        // 重新检查网盘资源（带密码）
        await checkNoteDisks(selectedNote.id, password);
        setPassword('');
      }
    } catch (error: any) {
      if (error.response?.status === 401) {
        setPasswordError('密码错误，请重试');
      } else {
        setPasswordError('验证失败，请稍后重试');
      }
    } finally {
      setVerifying(false);
    }
  };

  const closeModal = () => {
    setShowModal(false);
    setSelectedNote(null);
  };

  const closePasswordModal = () => {
    setShowPasswordModal(false);
    setSelectedNote(null);
    setPassword('');
    setPasswordError('');
  };


  // 检查笔记内容是否为纯URL
  const isPureUrl = (content: string): boolean => {
    if (!content) return false;

    // 去除前后空格
    const trimmedContent = content.trim();

    // URL正则表达式
    const urlRegex = /^https?:\/\/[\w\-\.]+\.[a-z]{2,}(\/\S*)?$/i;

    return urlRegex.test(trimmedContent);
  };

  // 从URL中提取域名
  const extractDomain = (url: string): string => {
    try {
      const domain = new URL(url).hostname;
      return domain.replace('www.', '');
    } catch {
      return '';
    }
  };

  // 获取网站图标URL - 使用多个备选方案避免429错误
  const getFaviconUrl = (url: string): string => {
    const domain = extractDomain(url);
    // 使用多个备选方案，避免单一API的429错误
    const faviconUrls = [
      `https://${domain}/favicon.ico`,
      `https://www.${domain}/favicon.ico`,
      `https://icons.duckduckgo.com/ip3/${domain}.ico`,
      `https://favicon.yandex.net/favicon/${domain}`
    ];
    return faviconUrls[0]; // 使用第一个方案，如果失败会fallback
  };

  // 获取URL元数据
  const fetchUrlMetadata = async (url: string) => {
    if (urlMetadata[url]) {
      return urlMetadata[url];
    }

    try {
      // 使用后端API获取元数据，避免CORS问题
      const response = await safeFetch(`/api/metadata?url=${encodeURIComponent(url)}`);
      if (response.ok) {
        const metadata = await response.json();
        setUrlMetadata(prev => ({
          ...prev,
          [url]: {
            title: metadata.title || extractDomain(url),
            description: metadata.description || '点击卡片访问网站',
            favicon: metadata.favicon || getFaviconUrl(url),
            image: metadata.image || ''
          }
        }));
        return metadata;
      }
    } catch (error) {
      console.error('Failed to fetch URL metadata:', error);
    }

    // 如果API失败，使用默认值
    const defaultMetadata = {
      title: extractDomain(url),
      description: '点击卡片访问网站',
      favicon: getFaviconUrl(url),
      image: ''
    };

    setUrlMetadata(prev => ({
      ...prev,
      [url]: defaultMetadata
    }));

    return defaultMetadata;
  };

  // 处理标签点击筛选
  const handleTagClick = (tagName: string) => {
    // 如果在首页，跳转到 /notes 页面并带上筛选参数
    if (isHomePage) {
      window.location.href = `/notes?tag=${encodeURIComponent(tagName)}`;
    } else {
      // 如果已经在 /notes 页面，使用内部筛选
      setFilter({
        type: 'tag',
        value: tagName
      });
      setPage(1); // 重置到第一页
    }
  };

  // 处理分类点击筛选
  const handleCategoryClick = (categoryName: string) => {
    // 如果在首页，跳转到 /notes 页面并带上筛选参数
    if (isHomePage) {
      window.location.href = `/notes?category=${encodeURIComponent(categoryName)}`;
    } else {
      // 如果已经在 /notes 页面，使用内部筛选
      setFilter({
        type: 'category',
        value: categoryName
      });
      setPage(1); // 重置到第一页
    }
  };

  // 清除筛选
  const clearFilter = () => {
    setFilter({
      type: null,
      value: null
    });
    setPage(1); // 重置到第一页
  };

  // 加载设置
  const loadSettings = async () => {
    try {
      const response = await api.get('/settings');
      if (response.data.success && response.data.data) {
        const settingsData = response.data.data;
        const showNoteTagsSetting = settingsData.find((setting: any) => setting.key === 'showNoteTags');
        const showNoteCategoriesSetting = settingsData.find((setting: any) => setting.key === 'showNoteCategories');
        const noteDisplayModeSetting = settingsData.find((setting: any) => setting.key === 'noteDisplayMode');
        const noteLayoutColumnsSetting = settingsData.find((setting: any) => setting.key === 'noteLayoutColumns');
        const showNoteCoverSetting = settingsData.find((setting: any) => setting.key === 'showNoteCover');
        const defaultNoteCoverSetting = settingsData.find((setting: any) => setting.key === 'defaultNoteCover');

        setSettings({
          showNoteTags: showNoteTagsSetting?.value !== 'false',
          showNoteCategories: showNoteCategoriesSetting?.value !== 'false',
          noteDisplayMode: noteDisplayModeSetting?.value || 'modal',
          noteLayoutColumns: noteLayoutColumnsSetting?.value || '1',
          showNoteCover: showNoteCoverSetting?.value || 'true',
          defaultNoteCover: defaultNoteCoverSetting?.value || ''
        });
      }
    } catch (error) {
      console.error('Failed to load settings:', error);
    }
  };

  // 在组件挂载时预加载URL元数据
  useEffect(() => {
    const loadUrlMetadata = async () => {
      const urlNotes = notes.filter(note => isPureUrl(note.content));
      for (const note of urlNotes) {
        await fetchUrlMetadata(note.content.trim());
      }
    };

    if (notes.length > 0) {
      loadUrlMetadata();
    }
  }, [notes]);

  // 过滤笔记基于搜索查询
  const filteredNotes = notes.filter(note => {
    if (!searchQuery.trim()) return true;

    const query = searchQuery.toLowerCase();
    const titleMatch = note.title?.toLowerCase().includes(query);
    const contentMatch = stripMarkdown(note.content).toLowerCase().includes(query);
    const categoryMatch = note.category?.toLowerCase().includes(query);

    // 检查标签匹配
    const tagsMatch = note.Tags?.some(tag => tag.name.toLowerCase().includes(query)) ||
                     note.tags?.toLowerCase().includes(query);

    return titleMatch || contentMatch || categoryMatch || tagsMatch;
  });

  // 创建分类名称到分类对象的映射
  const categoryMap = new Map(categories.map(cat => [cat.name, cat]));

  // 布局规则辅助函数
  const columns = parseInt(settings.noteLayoutColumns) || 1;

  // 是否使用横向布局（封面在左侧）
  const useHorizontalLayout = () => {
    if (columns === 1) return true; // 1列：所有设备横向
    if (columns === 2 || columns === 3) return isDesktop; // 2-3列：电脑端横向，手机端纵向
    return false; // 4列：所有设备纵向
  };

  // 是否显示标签和分类
  const shouldShowTagsAndCategories = () => {
    if (!isDesktop && columns >= 2) return false; // 手机端2列及以上不显示
    if (isDesktop && columns === 4) return false; // 电脑端4列不显示
    return true;
  };

  // 是否显示摘要
  const shouldShowSummary = () => {
    if (!isDesktop && columns >= 2) return false; // 手机端2列不显示
    if (isDesktop && columns === 4) return false; // 电脑端4列不显示
    return true;
  };

  const isHorizontal = useHorizontalLayout();
  const showTagsAndCats = shouldShowTagsAndCategories();
  const showSummary = shouldShowSummary();

  if (loading) {
    return (
      <div className="space-y-3">
        {[1, 2, 3].map((i) => (
          <div key={i} className="p-4 bg-bg-secondary/50 rounded-lg">
            <div className="h-5 w-3/4 bg-bg-tertiary/50 animate-pulse mb-3 rounded"></div>
            <div className="h-4 w-full bg-bg-tertiary/50 animate-pulse mb-2 rounded"></div>
            <div className="h-4 w-2/3 bg-bg-tertiary/50 animate-pulse mb-3 rounded"></div>
            <div className="flex items-center justify-between text-xs">
              <div className="h-3 w-24 bg-bg-tertiary/50 animate-pulse rounded"></div>
              <div className="h-3 w-16 bg-bg-tertiary/50 animate-pulse rounded"></div>
            </div>
          </div>
        ))}
      </div>
    );
  }

  if (notes.length === 0) {
    return null;
  }

  // 显示搜索结果的提示
  const showSearchResults = searchQuery.trim() && filteredNotes.length > 0;
  const showNoResults = searchQuery.trim() && filteredNotes.length === 0;

  return (
    <>
      {/* 搜索结果显示 */}
      {showSearchResults && (
        <div className="mb-4 p-3 bg-blue-50/50 rounded-lg border border-blue-200/50 flex items-center justify-between">
          <div className="flex items-center gap-2">
            <span className="text-sm text-blue-700">
              搜索结果: 找到 {filteredNotes.length} 条相关笔记
              <span className="font-medium ml-1">"{searchQuery}"</span>
            </span>
          </div>
        </div>
      )}

      {/* 无搜索结果提示 */}
      {showNoResults && (
        <div className="mb-4 p-3 bg-bg-tertiary/50 rounded-lg border border-border-primary/50 text-center">
          <span className="text-sm text-text-tertiary">
            没有找到包含 "{searchQuery}" 的笔记
          </span>
        </div>
      )}

      {/* 筛选状态指示器 */}
      {filter.type && filter.value && (
        <div className="mb-4 p-3 bg-bg-secondary/50 rounded-lg border border-border-primary/20 flex items-center justify-between">
          <div className="flex items-center gap-2">
            <span className="text-sm text-text-secondary">
              当前筛选:
              <span className="font-medium text-text-primary ml-1">
                {filter.type === 'tag' ? '标签' : '分类'}: {filter.value}
              </span>
            </span>
          </div>
          <button
            onClick={clearFilter}
            className="text-xs text-text-tertiary hover:text-text-primary transition-colors px-2 py-1 hover:bg-bg-tertiary/50 rounded"
          >
            清除筛选
          </button>
        </div>
      )}

      <Masonry
        breakpointCols={{
          default: parseInt(settings.noteLayoutColumns) || 1,
          1536: parseInt(settings.noteLayoutColumns) || 1,
          1280: parseInt(settings.noteLayoutColumns) === 4 ? 3 : parseInt(settings.noteLayoutColumns) || 1,
          1024: parseInt(settings.noteLayoutColumns) >= 3 ? 2 : parseInt(settings.noteLayoutColumns) || 1,
          768: parseInt(settings.noteLayoutColumns) > 1 ? 2 : 1,
          640: parseInt(settings.noteLayoutColumns) > 1 ? 2 : 1
        }}
        className="flex -ml-3 w-auto"
        columnClassName="pl-3 bg-clip-padding"
      >
        {filteredNotes.map((note, index) => (
          <motion.div
            key={note.id}
            className={`group block rounded-lg shadow-sm hover:shadow-lg hover:-translate-y-0.5 border transition-all duration-300 cursor-pointer overflow-hidden mb-3 ${
              isAdmin
                ? 'bg-white border-gray-200'
                : 'bg-bg-secondary border-border-primary'
            }`}
            initial={{ opacity: 0, y: 10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: index * 0.05 }}
            onClick={() => handleNoteClick(note)}
          >
            {/* 横向布局（封面在左侧） - 1列 或 2-3列的电脑端 */}
            {isHorizontal ? (
              <div className="flex gap-3 items-stretch p-3 h-32">
                {/* 左侧封面图 - 1:1正方形（仅当有封面图时显示） */}
                {(note.cover_image || settings.defaultNoteCover) && settings.showNoteCover === 'true' && (
                  <div className="flex-shrink-0 w-26 h-26 rounded-md overflow-hidden bg-bg-tertiary">
                    <img
                      src={(note.cover_image || settings.defaultNoteCover || '').startsWith('http')
                        ? (note.cover_image || settings.defaultNoteCover)
                        : `${API_BASE_URL}${note.cover_image || settings.defaultNoteCover}`}
                      alt={note.title || '封面图'}
                      className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                      onError={(e) => {
                        const target = e.target as HTMLImageElement;
                        target.style.display = 'none';
                      }}
                    />
                  </div>
                )}

                {/* 右侧内容 */}
                <div className="flex-1 flex flex-col justify-between min-w-0 py-1">
                  {/* 标题和置顶标签 */}
                  <div className="flex items-start gap-2 mb-1.5">
                    {note.title && (
                      <h3 className="text-base font-semibold text-text-primary line-clamp-2 flex-1 group-hover:text-primary-500 transition-colors leading-snug">
                        {note.title}
                      </h3>
                    )}
                    {note.is_pinned && (
                      <div className="flex-shrink-0 px-1.5 py-0.5 bg-gradient-to-r from-red-500 to-pink-500 text-white rounded text-[10px] font-semibold flex items-center gap-0.5">
                        <Pin className="w-2.5 h-2.5 fill-current" />
                      </div>
                    )}
                  </div>

                  {/* 摘要/内容预览 */}
                  {showSummary && (
                    <p className="text-xs text-text-secondary line-clamp-2 mb-1.5 leading-relaxed opacity-80">
                      {note.summary || (note.content ? truncate(stripMarkdown(note.content), 60) : '暂无内容')}
                    </p>
                  )}

                  {/* 底部：时间、分类、标签 */}
                  <div className="flex items-center gap-1.5 flex-wrap text-[10px] mt-auto">
                    <div className="flex items-center gap-1 text-text-tertiary">
                      <Clock className="w-3 h-3" />
                      <span className="text-[10px]">{formatDateTime(note.published_at || note.created_at)}</span>
                    </div>

                    {showTagsAndCats && settings.showNoteCategories && note.category && (
                      <span
                        className="px-1.5 py-0.5 bg-primary-100 text-primary-700 rounded-full font-medium cursor-pointer hover:bg-primary-200 transition-colors text-[10px]"
                        onClick={(e) => {
                          e.stopPropagation();
                          handleCategoryClick(note.category!);
                        }}
                      >
                        {categoryMap.get(note.category)?.icon && (
                          <span className="mr-0.5">{categoryMap.get(note.category)?.icon}</span>
                        )}
                        {note.category}
                      </span>
                    )}

                    {showTagsAndCats && settings.showNoteTags && note.Tags && note.Tags.length > 0 && (
                      <>
                        {note.Tags.slice(0, 2).map((tag) => (
                          <span
                            key={tag.id}
                            className="px-1.5 py-0.5 rounded-full font-medium cursor-pointer hover:opacity-80 transition-opacity text-[10px]"
                            style={tag.color ? { backgroundColor: `${tag.color}20`, color: tag.color } : { backgroundColor: 'rgb(242 242 242)', color: 'rgb(112 112 112)' }}
                            onClick={(e) => {
                              e.stopPropagation();
                              handleTagClick(tag.name);
                            }}
                          >
                            {tag.name}
                          </span>
                        ))}
                        {note.Tags.length > 2 && (
                          <span className="px-1.5 py-0.5 rounded-full text-[10px]" style={{ backgroundColor: 'rgb(242 242 242)', color: 'rgb(112 112 112)' }}>
                            +{note.Tags.length - 2}
                          </span>
                        )}
                      </>
                    )}
                  </div>
                </div>
              </div>
            ) : (
              /* 纵向布局（封面在上方） - 4列 或 2-3列的手机端 */
              <div className="flex flex-col h-full">
                {/* 封面图 - 1:1正方形 */}
                {(note.cover_image || settings.defaultNoteCover) && settings.showNoteCover === 'true' && (
                  <div className="relative w-full pt-[100%] overflow-hidden bg-gradient-to-br from-primary-100 to-primary-200 flex-shrink-0">
                    <img
                      src={(note.cover_image || settings.defaultNoteCover || '').startsWith('http')
                        ? (note.cover_image || settings.defaultNoteCover)
                        : `${API_BASE_URL}${note.cover_image || settings.defaultNoteCover}`}
                      alt={note.title || '封面图'}
                      className="absolute inset-0 w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                      onError={(e) => {
                        const target = e.target as HTMLImageElement;
                        target.style.display = 'none';
                      }}
                    />

                    {/* 置顶标识 */}
                    {note.is_pinned && (
                      <div className="absolute top-2 right-2 bg-black/50 backdrop-blur-sm rounded-full px-2 py-1 text-[10px] text-white font-semibold flex items-center gap-1">
                        <Pin className="w-2.5 h-2.5 fill-current" />
                        <span>置顶</span>
                      </div>
                    )}
                  </div>
                )}

                {/* 标题和描述 */}
                <div className="p-2.5 flex flex-col flex-1 min-h-0">
                  {note.title && (
                    <h3 className="font-semibold text-base text-text-primary group-hover:text-primary-500 transition-colors line-clamp-2 mb-1.5 leading-snug">
                      {note.title}
                    </h3>
                  )}

                  {/* 摘要 */}
                  {showSummary && (
                    <p className="text-xs text-text-secondary line-clamp-2 leading-relaxed mb-2 opacity-80">
                      {note.summary || (note.content ? truncate(stripMarkdown(note.content), 60) : '暂无内容')}
                    </p>
                  )}

                  {/* 底部信息 */}
                  <div className="flex items-center justify-between gap-1.5 text-[10px] mt-auto">
                    <div className="flex items-center gap-1 text-text-tertiary">
                      <Clock className="w-3 h-3" />
                      <span className="text-[10px]">{formatDateTime(note.published_at || note.created_at)}</span>
                    </div>

                    {showTagsAndCats && (
                      <div className="flex items-center gap-1 flex-wrap">
                        {settings.showNoteCategories && note.category && (
                          <span
                            className="px-1.5 py-0.5 bg-primary-100 text-primary-700 rounded-full font-medium cursor-pointer hover:bg-primary-200 transition-colors text-[10px]"
                            onClick={(e) => {
                              e.stopPropagation();
                              handleCategoryClick(note.category!);
                            }}
                          >
                            {categoryMap.get(note.category)?.icon && (
                              <span className="mr-0.5">{categoryMap.get(note.category)?.icon}</span>
                            )}
                            {note.category}
                          </span>
                        )}

                        {settings.showNoteTags && note.Tags && note.Tags.length > 0 && (
                          <>
                            {note.Tags.slice(0, 1).map((tag) => (
                              <span
                                key={tag.id}
                                className="px-1.5 py-0.5 rounded-full font-medium cursor-pointer hover:opacity-80 transition-opacity text-[10px]"
                                style={tag.color ? { backgroundColor: `${tag.color}20`, color: tag.color } : { backgroundColor: 'rgb(242 242 242)', color: 'rgb(112 112 112)' }}
                                onClick={(e) => {
                                  e.stopPropagation();
                                  handleTagClick(tag.name);
                                }}
                              >
                                {tag.name}
                              </span>
                            ))}
                            {note.Tags.length > 1 && (
                              <span className="px-1.5 py-0.5 rounded-full text-[10px]" style={{ backgroundColor: 'rgb(242 242 242)', color: 'rgb(112 112 112)' }}>
                                +{note.Tags.length - 1}
                              </span>
                            )}
                          </>
                        )}
                      </div>
                    )}
                  </div>
                </div>
              </div>
            )}
          </motion.div>
        ))}
      </Masonry>

    {totalPages > 1 && (
      <div className="flex justify-center gap-2 pt-6 mt-4">
        <button
            onClick={() => setPage((p) => Math.max(1, p - 1))}
            disabled={page === 1}
            className="px-3 py-1.5 rounded-lg bg-bg-primary text-text-secondary hover:bg-bg-secondary disabled:opacity-30 disabled:cursor-not-allowed transition-all text-sm"
          >
            上一页
          </button>

          <div className="flex items-center gap-1.5">
            {Array.from({ length: Math.min(totalPages, 5) }, (_, i) => {
              let pageNum;
              if (totalPages <= 5) {
                pageNum = i + 1;
              } else if (page <= 3) {
                pageNum = i + 1;
              } else if (page >= totalPages - 2) {
                pageNum = totalPages - 4 + i;
              } else {
                pageNum = page - 2 + i;
              }

              const isActive = pageNum === page;
              const btnClass = 'w-8 h-8 rounded-lg transition-all text-sm ' + (isActive ? 'bg-primary-500 text-white' : 'bg-bg-primary text-text-secondary hover:bg-bg-secondary');

              return (
                <button
                  key={pageNum}
                  onClick={() => setPage(pageNum)}
                  className={btnClass}
                >
                  {pageNum}
                </button>
              );
            })}
          </div>

          <button
            onClick={() => setPage((p) => Math.min(totalPages, p + 1))}
            disabled={page === totalPages}
            className="px-3 py-1.5 rounded-lg bg-bg-primary text-text-secondary hover:bg-bg-secondary disabled:opacity-30 disabled:cursor-not-allowed transition-all text-sm"
          >
            下一页
          </button>
        </div>
      )}

      {/* 密码验证弹窗 */}
      {showPasswordModal && selectedNote && (
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-[9999] p-4">
          <motion.div
            className="bg-bg-secondary rounded-2xl max-w-md w-full p-6 border border-border-primary shadow-2xl"
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.2 }}
          >
            <div className="text-center mb-6">
              <div className="w-16 h-16 bg-primary-100 rounded-full flex items-center justify-center mx-auto mb-4">
                <svg className="w-8 h-8 text-primary-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 15v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2zm10-10V7a4 4 0 00-8 0v4h8z" />
                </svg>
              </div>
              <h3 className="text-xl font-bold text-text-primary mb-2">此笔记需要密码</h3>
              <p className="text-sm text-text-secondary">请输入访问密码以查看内容</p>
            </div>

            <form onSubmit={handlePasswordSubmit} className="space-y-4">
              <div>
                <input
                  type="password"
                  name="note-access-password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  placeholder="请输入密码"
                  className="w-full px-4 py-3 border border-border-primary rounded-lg bg-bg-primary text-text-primary focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                  autoFocus
                  autoComplete="off"
                  disabled={verifying}
                />
                {passwordError && (
                  <p className="mt-2 text-sm text-red-600">{passwordError}</p>
                )}
              </div>

              <div className="flex gap-3">
                <button
                  type="button"
                  onClick={closePasswordModal}
                  className="flex-1 px-4 py-3 bg-bg-tertiary text-text-secondary rounded-lg hover:bg-bg-tertiary/80 transition-colors"
                  disabled={verifying}
                >
                  取消
                </button>
                <button
                  type="submit"
                  className="flex-1 px-4 py-3 bg-primary-500 text-white rounded-lg hover:bg-primary-600 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                  disabled={verifying || !password}
                >
                  {verifying ? '验证中...' : '确认'}
                </button>
              </div>
            </form>
          </motion.div>
        </div>
      )}

      {/* 笔记详情弹窗 */}
      {showModal && selectedNote && (
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-[9999] p-4">
          <motion.div
            className="bg-bg-primary rounded-2xl max-w-4xl w-full max-h-[80vh] overflow-hidden flex flex-col border border-border-primary"
            style={{ boxShadow: '0 25px 50px -12px rgba(0, 0, 0, 0.15)' }}
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.2 }}
          >
            {/* 弹窗头部 - 固定在顶部 */}
            <div className="bg-bg-tertiary px-5 pt-6 pb-4 flex items-center justify-between sticky top-0 z-50">
              <div className="flex-1 min-w-0">
                <div className="flex items-center gap-2 mb-1.5">
                  <h3 className="text-xl font-bold text-text-primary truncate">{selectedNote.title}</h3>
                  {selectedNote.source_type === 'original' && (
                    <div className="inline-flex items-center gap-1 px-2 py-0.5 bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-700/50 rounded text-[10px] font-medium flex-shrink-0">
                      <svg className="w-2.5 h-2.5 text-green-600 dark:text-green-400 flex-shrink-0" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                      </svg>
                      <span className="text-green-700 dark:text-green-300">原创</span>
                    </div>
                  )}
                  {selectedNote.source_type === 'reprint' && (
                    <div className="inline-flex items-center gap-1 px-2 py-0.5 bg-orange-50 dark:bg-orange-900/20 border border-orange-200 dark:border-orange-700/50 rounded text-[10px] font-medium flex-shrink-0">
                      <svg className="w-2.5 h-2.5 text-orange-600 dark:text-orange-400 flex-shrink-0" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13.828 10.172a4 4 0 00-5.656 0l-4 4a4 4 0 105.656 5.656l1.102-1.101m-.758-4.899a4 4 0 005.656 0l4-4a4 4 0 00-5.656-5.656l-1.1 1.1" />
                      </svg>
                      <span className="text-orange-700 dark:text-orange-300">转载</span>
                    </div>
                  )}
                  {selectedNote.content && selectedNote.content.includes('[网盘资源]') && (
                    <div className="inline-flex items-center gap-1 px-2 py-0.5 bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-700/50 rounded text-[10px] font-medium flex-shrink-0">
                      <svg className="w-2.5 h-2.5 text-blue-600 dark:text-blue-400 flex-shrink-0" fill="currentColor" viewBox="0 0 20 20">
                        <path d="M2 6a2 2 0 012-2h5l2 2h5a2 2 0 012 2v6a2 2 0 01-2 2H4a2 2 0 01-2-2V6z" />
                      </svg>
                      <span className="text-blue-700 dark:text-blue-300">含网盘资源</span>
                    </div>
                  )}
                </div>
                <div className="flex items-center gap-3 text-xs text-text-tertiary">
                  <div className="flex items-center gap-1">
                    <Clock className="w-3.5 h-3.5" />
                    <span>{formatDateTime(selectedNote.published_at || selectedNote.created_at)}</span>
                  </div>

                  {/* 分类显示 */}
                  {settings.showNoteCategories && selectedNote.category && (
                    <span
                      className="px-2 py-0.5 bg-primary-100 text-primary-700 rounded-full font-medium cursor-pointer hover:bg-primary-200 transition-colors"
                      onClick={(e) => {
                        e.stopPropagation();
                        handleCategoryClick(selectedNote.category!);
                        closeModal(); // 关闭弹窗
                      }}
                    >
                      {selectedNote.category}
                    </span>
                  )}

                  {/* 标签显示 */}
                  {settings.showNoteTags && (
                    <div className="flex items-center gap-1">
                      {/* 显示tagList关联表的标签 */}
                      {selectedNote.Tags && selectedNote.Tags.length > 0 && (
                        <>
                          {selectedNote.Tags.map((tag) => (
                            <span
                              key={tag.id}
                              className="px-2 py-0.5 rounded-full font-medium cursor-pointer hover:bg-gray-200 transition-colors"
                              style={tag.color ? { backgroundColor: `${tag.color}20`, color: tag.color } : { backgroundColor: 'rgb(242 242 242)', color: 'rgb(112 112 112)' }}
                              onClick={(e) => {
                                e.stopPropagation();
                                handleTagClick(tag.name);
                                closeModal(); // 关闭弹窗
                              }}
                            >
                              {tag.name}
                            </span>
                          ))}
                        </>
                      )}

                      {/* 显示tags字符串格式的标签（兼容旧数据） */}
                      {(!selectedNote.Tags || selectedNote.Tags.length === 0) && selectedNote.tags && (
                        <>
                          {selectedNote.tags.split(',').map((tag, index) => (
                            <span
                              key={index}
                              className="px-2 py-0.5 rounded-full font-medium cursor-pointer hover:bg-gray-200 transition-colors"
                              style={{ backgroundColor: 'rgb(242 242 242)', color: 'rgb(112 112 112)' }}
                              onClick={(e) => {
                                e.stopPropagation();
                                handleTagClick(tag.trim());
                                closeModal(); // 关闭弹窗
                              }}
                            >
                              {tag.trim()}
                            </span>
                          ))}
                        </>
                      )}
                    </div>
                  )}
                </div>
              </div>
              <button
                onClick={closeModal}
                className="w-9 h-9 flex items-center justify-center rounded-full border-2 border-border-primary text-text-tertiary hover:text-text-primary hover:border-border-secondary hover:bg-bg-tertiary/50 transition-all flex-shrink-0 ml-3"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            {/* 弹窗内容 - 可滚动，主背景色 */}
            <div className="flex-1 overflow-y-auto p-5 bg-bg-secondary hide-scrollbar">
              <div className="prose prose-lg max-w-none dark:prose-invert
                prose-headings:text-text-primary
                prose-headings:font-bold
                prose-h1:text-2xl prose-h1:mb-4
                prose-h2:text-xl prose-h2:mb-3 prose-h2:mt-6
                prose-h3:text-lg prose-h3:mb-2 prose-h3:mt-4
                prose-h4:text-base prose-h4:mb-2 prose-h4:mt-3
                prose-p:text-text-primary prose-p:leading-relaxed prose-p:mb-4
                prose-a:text-primary-600 dark:prose-a:text-primary-400 prose-a:no-underline hover:prose-a:underline
                prose-blockquote:border-l-4 prose-blockquote:border-primary-500 prose-blockquote:bg-bg-tertiary/30 prose-blockquote:py-2 prose-blockquote:px-4 prose-blockquote:rounded-r-lg prose-blockquote:not-italic
                prose-strong:text-text-primary prose-strong:font-semibold
                prose-em:text-text-primary prose-em:italic
                prose-ul:list-disc prose-ul:pl-6 prose-ul:mb-4
                prose-ol:list-decimal prose-ol:pl-6 prose-ol:mb-4
                prose-li:text-text-primary prose-li:mb-1
                prose-table:w-full prose-table:border-collapse prose-table:my-4
                prose-th:bg-bg-tertiary prose-th:text-text-primary prose-th:font-semibold prose-th:p-2 prose-th:border prose-th:border-border-primary
                prose-td:p-2 prose-td:border prose-td:border-border-primary prose-td:text-text-primary
                prose-img:rounded-lg prose-img:shadow-md prose-img:mx-auto
                prose-hr:border-hr-color prose-hr:my-6
                whitespace-normal
              ">
                <ReactMarkdown
                  remarkPlugins={[remarkGfm]}
                  rehypePlugins={[rehypeRaw]}
                  components={{
                    img: ({ src, alt, ...props }) => {
                      let imageSrc = src;
                      if (typeof src === 'string' && src.startsWith('/uploads/')) {
                        imageSrc = `${API_BASE_URL}${src}`;
                      }
                      return (
                        <div className="flex justify-center my-4">
                          <img
                            src={imageSrc}
                            alt={alt || ''}
                            className="max-w-full h-auto rounded-lg shadow-md transition-all duration-300 hover:shadow-lg hover:scale-[1.02]"
                            style={{ maxHeight: '400px', objectFit: 'contain' }}
                            loading="lazy"
                            onError={(e) => {
                              (e.target as HTMLImageElement).style.display = 'none';
                            }}
                            {...props}
                          />
                        </div>
                      );
                    },
                    iframe: ({ node, ...props }) => (
                      <div className="my-4 rounded-lg overflow-hidden shadow-md">
                        <iframe className="w-full aspect-video" {...props} />
                      </div>
                    ),
                    pre: ({ node, children, ...props }: any) => {
                      const codeText = React.Children.toArray(children).join('');
                      return (
                        <div className="relative my-4 group">
                          <pre className="bg-code-block-bg text-code-block-text p-3 rounded-lg overflow-x-auto shadow-sm font-mono text-sm leading-relaxed" {...props}>
                            {children}
                          </pre>
                          <CopyButton text={codeText} />
                        </div>
                      );
                    },
                    code: ({ node, inline, className, children, ...props }: any) => {
                      if (inline) {
                        return (
                          <code {...props}>
                            {children}
                          </code>
                        );
                      }
                      return (
                        <code className={`text-sm font-mono ${className || ''}`} {...props}>
                          {children}
                        </code>
                      );
                    },
                    table: ({ node, children, ...props }: any) => (
                      <div className="overflow-x-auto my-4 rounded shadow-sm">
                        <table className="w-full min-w-full" {...props}>
                          {children}
                        </table>
                      </div>
                    ),
                    blockquote: ({ node, children, ...props }: any) => (
                      <blockquote className="border-l-4 border-primary-500 bg-bg-tertiary/30 py-2 px-4 rounded-r-lg my-4 text-text-primary italic" {...props}>
                        {children}
                      </blockquote>
                    ),
                    a: ({ node, href, children, ...props }: any) => (
                      <a
                        href={href}
                        className="text-primary-600 dark:text-primary-400 no-underline hover:underline transition-colors duration-200 font-medium"
                        target="_blank"
                        rel="noopener noreferrer"
                        {...props}
                      >
                        {children}
                      </a>
                    ),
                    p: ({ node, children, ...props }: any) => {
                      // 检查children是否包含文本节点
                      const hasTextNodes = React.Children.toArray(children).some(child =>
                        typeof child === 'string' && child.includes('\n')
                      );

                      // 如果包含换行符的文本节点，则处理换行
                      if (hasTextNodes) {
                        return (
                          <p className="text-text-primary leading-relaxed mb-4 whitespace-normal break-words" {...props}>
                            {React.Children.map(children, (child, index) => {
                              if (typeof child === 'string') {
                                const lines = child.split('\n');
                                return (
                                  <React.Fragment key={index}>
                                    {lines.map((line, lineIndex) => (
                                      <React.Fragment key={lineIndex}>
                                        {line}
                                        {lineIndex < lines.length - 1 && <br />}
                                      </React.Fragment>
                                    ))}
                                  </React.Fragment>
                                );
                              }
                              return child;
                            })}
                          </p>
                        );
                      }

                      // 普通段落
                      return (
                        <p className="text-text-primary leading-relaxed mb-4 whitespace-normal break-words" {...props}>
                          {children}
                        </p>
                      );
                    },
                  }}
                >
                  {selectedNote.content}
                </ReactMarkdown>

                {/* 文章来源信息 */}
                {selectedNote.source_type === 'reprint' && selectedNote.source_text && (
                  <div className="mt-8 pt-6 border-t border-dashed border-border-primary/15">
                    <div className="text-sm text-text-tertiary">
                      <span className="font-medium">文章来源：</span>
                      <span className="ml-1">{selectedNote.source_text}</span>
                    </div>
                  </div>
                )}
              </div>
            </div>

            {/* 弹窗底部 - 固定在底部 */}
            <div className="bg-bg-tertiary px-5 py-4 flex justify-between items-center sticky bottom-0 z-50">
              <div className="flex gap-2">
                {hasDisks && (
                  <button
                    onClick={() => setShowDiskModal(true)}
                    className="px-4 py-2 bg-blue-500 text-white text-sm rounded-lg hover:bg-blue-600 transition-all flex items-center gap-2"
                  >
                    <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                      <path d="M2 6a2 2 0 012-2h5l2 2h5a2 2 0 012 2v6a2 2 0 01-2 2H4a2 2 0 01-2-2V6z" />
                    </svg>
                    下载资源
                  </button>
                )}
                <button
                  onClick={() => setShowShareCard(true)}
                  className="px-4 py-2 bg-gradient-to-r from-purple-600 to-indigo-600 text-white text-sm rounded-lg hover:from-purple-700 hover:to-indigo-700 transition-all flex items-center gap-2"
                >
                  <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8.684 13.342C8.886 12.938 9 12.482 9 12c0-.482-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.316a3 3 0 105.368 2.684 3 3 0 00-5.368-2.684z" />
                  </svg>
                  分享笔记
                </button>
              </div>
              <button
                onClick={closeModal}
                className="px-4 py-2 bg-primary-500 text-white text-sm rounded-lg hover:bg-primary-600 transition-all"
              >
                关闭
              </button>
            </div>
          </motion.div>
        </div>
      )}

      {/* 网盘管理弹窗 */}
      {selectedNote && (
        <DiskModal
          isOpen={showDiskModal}
          onClose={() => setShowDiskModal(false)}
          noteId={selectedNote.id}
          isAdmin={false}
          password={verifiedPassword}
        />
      )}

      {/* 分享卡片 */}
      {showShareCard && selectedNote && (
        <NoteShareCard
          noteId={selectedNote.id}
          title={selectedNote.title}
          password={selectedNote.password}
          onClose={() => setShowShareCard(false)}
        />
      )}
    </>
  );
}
